
ul li {
    list-style: none;
}
.ask span {
    display: inline-block;
    padding: 10px;
    line-height: 30px;
    background-color: #eee;
    margin-left: 10px;
    margin-top: 20px;
    border-radius: 8px;
}
.answer{
    overflow: hidden;
}
.answer span {
    display: inline-block;
    padding: 10px;
    float: right;
    line-height: 30px;
    background-color: #eee;
    margin-right: 10px;
    margin-top: 20px;
    border-radius: 8px;
    margin-left: 10px;
}
.msgContainer {
    margin-top: 48px;
}
.topbar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    background-color: #fc378c;
    height: 48px
}
.sendComment {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50px;
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}
.sendComment input {
    float: left;
    margin-top: 14px;
    width: 80%;
    padding-left: 10px;
    border-radius: 4px;
    border: 1px solid #ddd;
    margin-left: 10px
}
.sendComment button {
    float: left;
    margin-top: 14px;
    border: 1px solid #ddd;
    margin-left: 6px;
    width: 50px;
    border-radius: 4px;
}



