
.weui-mask_transparent[data-v-2345d21c] {
    display: none;
}
.weui-mask_transparent.active[data-v-2345d21c] {
    display: block;
    background-color: rgba(0, 0, 0, .4);
}
.play[data-v-2345d21c]{
    width: 100%;
    z-index: 10;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 60px;
    background-color: #fff;
    box-shadow: 0 0 5px 1px rgba(0,0,0,0.1);
}
.play h4[data-v-2345d21c] {
    position: absolute;
    left: 67px;
    top: 10px;
    color: #333;
    font-weight: 700;
}
.play p[data-v-2345d21c] {
    position: absolute;
    left: 67px;
    top: 30px;
    color: #999;
}
img[data-v-2345d21c] {
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    top: 5px;
    left: 8px;
    z-index: 100;
}
.playButton[data-v-2345d21c] {
    background-image: url('../../static/images/auto.png');
    background-size: 30px 60px;
    display: block;
    background-position: 0 0px;
    width: 30px;
    height: 30px;
    background-repeat: no-repeat;
    position: absolute;
    top: 15px;
    right: 50px;
    z-index: 100;
}
.playButton.active[data-v-2345d21c] {
    background-position: 0 -30px;
    top: 14px;
}
.more[data-v-2345d21c] {
    background-image: url('../assets/moreContent.png');
    background-size: 28px 28px;
    display: block;
    background-position: center;
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    position: absolute;
    top: 1px;
    right: 4px;
    z-index: 100;
}

/*播放列表样式*/
.weui-actionsheet[data-v-2345d21c] {
    background-color: #fff;
}
.weui-actionsheet__menu[data-v-2345d21c] {
    margin: 0 16px;
    background-color: #fff;
}
.title[data-v-2345d21c] {
    font-size: 16px;
    margin-left: 4px;
    line-height: 45px;
    text-align: left;
}
.weui-actionsheet__cell[data-v-2345d21c] {
    font-size: 16px;
    /*text-align: left;*/
}
.weui-actionsheet__action[data-v-2345d21c] {
    margin: 0 16px;
    background-color: #fff;
}
#actionSheet_wrap .cancel[data-v-2345d21c] {
    content: " ";
    border-top: 1px solid #e5e5e5;
    color: #000;
}
span.icondiy-play[data-v-2345d21c] {
    background-image: url("http:////static0.qianqian.com/web/st/img/ui/list/play-state-aa7.gif");
    display: inline-block;
    width: 12px;
    height: 12px;
    background-size: 12px;
    -webkit-transition: margin .2s;
    transition: margin .2s;
    -webkit-transition-property: margin;
    transition-property: margin;
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;
    -webkit-transition-timing-function: initial;
            transition-timing-function: initial;
    -webkit-transition-delay: initial;
            transition-delay: initial;
    position: absolute;
    left: 7px;
    top: 16px;
}
i.icon-xiazai1[data-v-2345d21c] {
    color: #999;
    position: absolute;
    right: 0
}


/*音乐播放组件*/
section[data-v-2345d21c] {
    position: relative;
}
.play_audio[data-v-2345d21c] {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: -1111;
    opacity: 0
}
.songsName[data-v-2345d21c] {
    display: inline-block;
    width: 80%;
}


/*--------------进度条*/
#progress[data-v-2345d21c] {
    width: 100%;
    height: 2px;
    background: #ccc;
    position: absolute;
    top:0;
    z-index: 1000;
    border-radius: 10px
}
/*进度加载*/
#bar[data-v-2345d21c] {
    width: 0%;
    height: 100%;
    background-color:#fc378c;
    display: inline-block;
    position: absolute;
    top: 0
}



