
#song[data-v-21fe59ff] {
    background-color: #f8f8f8;
    overflow: hidden;
}
.header[data-v-21fe59ff] {
    height: 48px;
    margin-bottom: .8rem;
    margin-right: 16px;
    margin-left: 16px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    position: relative;
}
.icon-home[data-v-21fe59ff],
.icon-search[data-v-21fe59ff] {
    font-size: 29px;
    position: absolute;
    top: 2px;
}
.icon-home[data-v-21fe59ff] {
    left: 0;
}
.icon-search[data-v-21fe59ff] {
    right: 0;
}
.header-content[data-v-21fe59ff] {
    display: inline-block;
    width: 100%;
    text-align: center;
}
.header-content .title[data-v-21fe59ff] {
    font-size: 21px;
    line-height: 21px;
    margin-top: 15px;
}
.header-content .author[data-v-21fe59ff] {
    font-size: 14px;
    line-height: 14px;
    margin-top: 7px;
    color: #999;
}
.song-info[data-v-21fe59ff] {
    width: 100%;
    height: 333px;
    background-color: #f8f8f8;
    margin-top: 20px;
    overflow: hidden;
}
#picture[data-v-21fe59ff] {
    width: 166px;
    height: 166px;
    background-color: #fff;
    -webkit-align-self: center;
            align-self: center;
    margin-left: 110px;
    margin-top: 30px;
    border-radius: 50%;
    box-shadow: 0 0 10px 2px rgba(50, 50, 50, .31);
}
.zhuan[data-v-21fe59ff] {
    -webkit-animation: "loading" 3s infinite linear;
            animation: "loading" 3s infinite linear
}
#picture img[data-v-21fe59ff] {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.lyric[data-v-21fe59ff] {
    height: 80px;
    text-align: center;
    margin-top: 15px;
    overflow: hidden;
    position: relative;
}
.icon-love[data-v-21fe59ff],
.icon-xiazai1[data-v-21fe59ff] {
    padding: 0 20px;
    font-size: 22px;
}
.play[data-v-21fe59ff] {
    padding: 0 20px;
    height: 240px;
    position: relative;
}
#skPlayer[data-v-21fe59ff] {
    position: absolute;
    left: 0;
    top: 0;
}
#skPlayer .skPlayer-picture .skPlayer-play-btn[data-v-21fe59ff] {
    left: 50px
}
.skPlayer-list[data-v-21fe59ff] {
    height: 135px;
}
.ad[data-v-21fe59ff] {
    width: 80%;
    height: 100px;
    text-align: center;
    position: absolute;
    bottom: 32px;
}
.ad img[data-v-21fe59ff] {
    width: 100%;
    height: 100%;
    margin-left: 42px;
    border-radius: 16px;
}
@-webkit-keyframes "loading" {
    /**关键帧名称**/
0% {
        -webkit-transform: rotate(0deg);
}
100% {
        -webkit-transform: rotate(360deg);
}
}


/*歌词滚动样式*/
.lrc-inner[data-v-21fe59ff] {
    position: absolute;
    margin-top: 10px;
    width: 100%;
    text-align: center;
    top: 0;
}
.lrc-inner[data-v-21fe59ff]:hover {
    top: -90px;
    -webkit-transition: all 4s linear 0s;
    transition: all 4s linear 0s
}
.lrc-inner li[data-v-21fe59ff] {
    list-style: none;
}
.font[data-v-21fe59ff] {
    -webkit-transition: all .0.8s linear 0s;
    transition: all .0.8s linear 0s;
    font-size: 18px;
}



